var getRmb = require('../../common/requset.js');
import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
	data() {
		return {
			labnrv: '',
			type: '卖出',
			datn: '',
			state: '',
			stw: '',
			price: '',
			rmb: '',
			frte: true,
			checkall: '展示完整信息',
			numberList: [],
			length: 6,
			passIn: false,
			password: '',
			nums: ''
		};
	},
	components: {
		keyboardPackage,
		passwordInput
	},
	onLoad(option) {
		var that = this;
		//获取上个页面携带的数据，并复制
		var datn = JSON.parse(option.aser);
		that.datn = datn;
		var stw = that.datn[1];
		that.stw = that.datn[1];

		that.labnrv = that.datn[0][0];
		that.state = that.datn[0][0].order_status;
		if (that.state) {
			that.state = '待确认';
		}
		that.price = that.datn[0][0].sale_money;
		if (that.state == 101) {
			that.state = '待付款';
		}
		that.rmb = getRmb.getrmb(that.price);
	},
	methods: {
		btn: function() {
			var that = this;
			that.frte = !that.frte;
			if (that.frte == false) {
				that.checkall = '收起';
			} else {
				that.checkall = '展示完整信息';
			}
		},
		clo: function() {
			this.passIn = false;
			this.$refs['number'].close();
			this.numberList.length = 0;
		},
		onDelete() {
			this.numberList.pop();
		},
		onChange(e) {
			if (e.show == false) {
				this.passIn = false;
			}
			this.numberList.length = 0;
		},
		onConfirm() {
			if (this.numberList.length != 6) {
				uni.showToast({
					title: '请输入六位交易密码！',
					duration: 2000,
					icon: 'none'
				});
			}
		},
		handleOrderConfirm: function(nums) {
			var that = this;
			this.nums = nums;
			console.log(this.nums);
			that.passIn = true;
			that.$refs['number'].open();
			that.onInput(val);
		},
		onInput(val) {
			var that = this;
			console.log(that.num1);
			that.numberList.push(val);
			that.password = that.numberList.join().replace(/,/g, '');
			if (that.numberList.length >= that.length) {
				uni.request({
					url: that.url + 'saleaffirm/',
					method: 'POST',
					data: {
						order_num: that.nums,
						password: that.password
					},
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						if (res.statusCode == 400) {
							that.numberList.pop();
							that.numberList.length = 0;
							that.$refs.wrong.flag = false;
							var n = res.data.data.err_num;
							var s = 5 - n;
							that.$refs.wrong.tip = '剩余' + s + '次机会';
						}
						if (res.statusCode == 423) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '交易密码已锁定,请在今日24:00后进行交易',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 201) {
							that.success = true;
						}
						if (res.statusCode == 200) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.navigateBack({
								delta: 2
							});
							uni.showToast({
								title: '收款完成',
								duration: 3000
							});
						}
						var page = getCurrentPages().pop();
						if (page == undefined || page == null) return;
						page.onLoad(that.val);
					}
				});
				this.numberList.length = 0;
			}
		}
	}
};
